<template>
    <div class="container">
        <h1>搜尋結果</h1>
        
        <div>
            <h2>在最新消息中找到{{ search.news.rows }}筆資料</h2>
            <div class="border rounded-lg p-2 my-1 shadow-sm bg-white rounded"
                v-for="(item, index) in search.news.data"
                :key="index"
            >
                <router-link :to="'/news/' + item.id">{{item.title}}</router-link>
            </div>
            <!--  -->
            <h2>在相簿中找到{{ search.album.rows }}筆資料</h2>
            <div class="border rounded-lg p-2 my-1 shadow-sm bg-white rounded" 
                v-for="(item, index) in search.album.data" 
                :key="index"
            >
                <router-link :to="'/album/' + item.id">{{item.name}}</router-link>
            </div>
            <!--  -->
            <h2>在榮譽榜中找到{{ search.honor.rows }}筆資料</h2>
            <table class="table table-hover" v-if="search.honor.rows != 0">
                <thead>
                    <tr>
                        <th scope="col" class="text-center">
                            科系
                        </th>
                        <th scope="col">標題</th>
                        <th scope="col" class="text-center">排名</th>
                        <th scope="col" class="text-center">發布日期</th>
                        <th scope="col" class="text-center">更新日期</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, index) in search.honor.data" :key="index">
                        <td class="text-center fs-5">
                            <b-badge v-if="item.department == 41" pill variant="dark">應用英文系</b-badge>
                            <b-badge v-if="item.department == 42" pill variant="dark">應用日文系</b-badge>
                            <b-badge v-if="item.department == 43" pill variant="dark">應用中文系</b-badge>
                        </td>
                        <td>{{ item.title }}</td>
                        <td class="text-center">
                            <span v-if="item.ranking_style == 1" class="badge rounded-pill bg-warning text-dark">
                                {{ item.ranking }}
                            </span>
                            <span v-if="item.ranking_style == 2" class="badge rounded-pill bg-light text-dark">
                                {{ item.ranking }}
                            </span>
                            <span v-if="item.ranking_style == 3" class="badge rounded-pill bg-secondary">
                                {{ item.ranking }}
                            </span>
                            <span v-if="item.ranking_style == 0" class="badge rounded-pill bg-primary">
                                {{ item.ranking }}
                            </span>
                        </td>
                        <td class="text-center">{{ moment(item.created_at).format("YYYY-MM-DD HH:MM") }}</td>
                        <td class="text-center">{{ moment(item.updated_at).format("YYYY-MM-DD HH:MM") }}</td>
                    </tr>
                </tbody>
            </table>
            <!--  -->
            <h2>在連結與檔案中找到{{ search.link.rows }}筆資料</h2>
            <div class="border rounded-lg p-2 my-1 shadow-sm bg-white rounded" 
                v-for="(item, index) in search.link.data" 
                :key="index"
            >
                <router-link :to="'/link/' + item.id">{{item.name}}</router-link>
            </div>
        </div>
    </div>
    <div class="text-center">
        <button type="button" class="btn btn-secondary my-3" @click="historyPrev">回上頁</button>
    </div>
</template>

<script>
import moment from 'moment';
import { SearchServer } from "../../providers/http-server.js";
export default {
    name: 'App',
    data() {
        return {
            search: {
                news: {
                    data: [],
                    rows: 0
                },
                album: {
                    data: [],
                    rows: 0
                },
                honor: {
                    data: [],
                    rows: 0
                },
                link: {
                    data: [],
                    rows: 0
                },
            },
            keywork: ''
        }
    },
    created(){
        this.moment = moment;

        this.keyword = this.$route.params.keyword;
        if(this.keyword != '')
            SearchServer.read({keyword: this.keyword}).then(
                (response) => {
                    console.log(response)
                    this.search = response.data;
                }
            );
    },
    updated() {
        this.keyword = this.$route.params.keyword;
        if(this.keyword != '')
            SearchServer.read({keyword: this.keyword}).then(
                (response) => {
                    console.log(response)
                    this.search = response.data;
                }
            );
    },
    methods: {
        historyPrev() {
            this.$router.go(-1);
        }
    }
};
</script>

<style scoped></style>